<template><section><html><head></head><body><h1>CylinderGraphics</h1>
<p>The vc-graphics-cylinder<code v-pre="">component is used to load cylinder, cone, or truncated cone. Need to be used as a subcomponent of vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load CylinderGraphics with vc-graphics-cylinder</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity1" :position="position1" :description="description" :cylinder.sync="cylinder1">
          <vc-graphics-cylinder :length="400000.0" :top-radius="200000.0" :bottom-radius="200000.0" :material="material1" :outline="true" :outline-color="outlineColor1"></vc-graphics-cylinder>
        </vc-entity>
        <vc-entity ref="entity2" :position="position2" :description="description" :cylinder.sync="cylinder2">
          <vc-graphics-cylinder :length="400000.0" :top-radius="0.0" :bottom-radius="200000.0" :material="material2"></vc-graphics-cylinder>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
