<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying the visibility of the cylinder.</td>
</tr>
<tr>
<td>length</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the length of the cylinder.</td>
</tr>
<tr>
<td>topRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the radius of the top of the cylinder.</td>
</tr>
<tr>
<td>bottomRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the radius of the bottom of the cylinder.</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying what the height from the entity position is relative to. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the cylinder is filled with the provided material.</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> A Property specifying the material used to fill the cylinder.</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the cylinder is outlined.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'BLACK'</code></td>
<td><code v-pre="">optional</code> A Property specifying the Color of the outline.</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the width of the outline.</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre="">16</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the number of vertical lines to draw along the perimeter for the outline.</td>
</tr>
<tr>
<td>slices</td>
<td>Number</td>
<td><code v-pre="">128</code></td>
<td><code v-pre="">optional</code> The number of edges around the perimeter of the cylinder.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> An enum Property specifying whether the cylinder casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying at what distance from the camera that this cylinder will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CylinderGraphics.html">CylinderGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          cylinder1: {},
          position1: { lng: 105.0, lat: 40.0, height: 200000.0 },
          outlineColor1: 'DARK_GREEN',
          material1: {},

          cylinder2: {},
          position2: { lng: 110.0, lat: 40.0, height: 200000.0 },
          material2: 'RED'
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material1 = Cesium.Color.GREEN.withAlpha(0.5)
        },
        subReady(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          viewer.zoomTo(viewer.entities)
        }
      }
    }
  </script>